.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
}

.copyright__text {
  font-weight: normal;
}

.copyright__link {
  display: inline-block;
  margin-left: 10px;
  font-size: 0;
  width: 27px;
  height: 34px;

  &:hover {
    fill: $logo-hover;
  }

  &:active {
    fill: $logo-active;
  }
}

@media (min-width: $tablet-width) {
  .copyright {
    margin-left: 10px;
  }

  .copyright__link {
    margin-left: 13px;
  }
}
