.video {
  position: relative;
  margin-top: 20px;
  margin-bottom: 85px;
}

.video__screen {
  vertical-align: top;
}

.video__track {
  position: absolute;
  bottom: 0;
  left: 20px;
  height: 45px;
  width: 280px;
  background-color: rgba(#000000, 0.6%);
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 25px;
    left: 25px;
    height: 3px;
    width: 84%;
    background: #ffffff;
    opacity: 0.2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 57px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #ffffff;
  }
}

.video__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: -65px;
  left: 20px;

  margin: 0 auto;
  padding: 22px;
  padding-left: 25px;
  height: 65px;
  width: 280px;
  box-sizing: border-box;
  background-color: #f2f2f2;
}

.video__btn {
  padding: 0;
  height: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &--captions {
    margin-left: 100px;
  }

  &:hover {
    .video__svg {
      fill: $controls_mobile-hover;
    }
  }

  &:active {
    .video__svg {
      fill: $controls_mobile-active;
    }
  }
}

.video__svg {
  fill: $controls_mobile;
}

@media (min-width: $tablet-width) {
  .video {
    margin-top: 75px;
    margin-bottom: 40px;
  }

  .video__screen {
    width: 648px;
    height: 500px;
  }

  .video__panels {
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0 auto;
    width: 100%;
    height: 95px;
    background-color: rgba(#000000, 0.6%);
  }

  .video__track {
    bottom: 25px;
    width: 390px;
    background-color: transparent;

    &::before {
      left: 75px;
      bottom: 25px;
      margin: auto 0;
      width: 390px;
    }

    &::after {
      top: 15px;
      left: 185px;
    }
  }

  .video__controls {
    flex-wrap: wrap;
    bottom: 0;
    left: 0;
    padding-top: 12px;
    padding-left: 35px;
    padding-right: 40px;
    height: 90px;
    width: 100%;
    background-color: transparent;
  }

  .video__btn--captions {
    margin-left: auto;
    margin-right: 30px;
  }

  .video__btn {
    &:hover {
      .video__svg {
        fill: $controls-hover;
      }
    }

    &:active {
      .video__svg {
        fill: $controls-active;
      }
    }
  }

  .video__svg {
    fill: $controls;
  }
}

@media (min-width: $desktop-width) {
  .video {
    margin-top: 70px;
    margin-bottom: 90px;
  }

  .video__screen {
    width: 800px;
  }

  .video__panels {
    height: 90px;
  }

  .video__track {
    bottom: 24px;
    width: 540px;

    &::before {
      width: 540px;
    }

    &::after {
      top: 17px;
    }
  }

  .video__controls {
    padding-top: 15px;
    padding-right: 44px;
  }
}
